@import '../../@theme/index';
$first_img: url('/assets/images/whoweare/whoweare.png');
$two_img: url('/assets/images/whoweare/image2.png');
$three_img: url('/assets/images/whoweare/image3.png');
$four_img: url('/assets/images/whoweare/image4.png');
$two_img_back: url('/assets/images/whoweare/back2.png');
$three_img_back: url('/assets/images/whoweare/back3.png');
$four_img_back: url('/assets/images/whoweare/back4.png');



.whoweare__secondblock {
    &:hover .whoweare__secondblock__image {            
        background-position: 0%;       
    }
    &:hover .whoweare__secondblock__button {
        bottom: 130px;
        opacity: 1;
    }
    &:hover .whoweare__secondblock__h {
        top: -200px;
        opacity: 1;
    }
    &:hover .whoweare__secondblock__p {
        left: 14%;
        opacity: 1;
    }
}
.whoweare__threeblock {
    &:hover .whoweare__threeblock__image {            
        background-position: 100% 0;       
    }
    &:hover .whoweare__threeblock__button {
        bottom: 250px;
        opacity: 1;
    }
    &:hover .whoweare__threeblock__h {
        top: 420px;
        opacity: 1;
    }
}
.whoweare__fourblock {
    &:hover .whoweare__fourblock__image {            
        background-position: 100% 0;       
    }
    &:hover .whoweare__fourblock__button {
        bottom: 270px;
        opacity: 1;
    }
    &:hover .whoweare__fourblock__head {
        transform: translateY(100px);
        opacity: 1;
    }
    &:hover .whoweare__fourblock__p {
        left: 14%;
        opacity: 1;
    }
    &:hover .whoweare__fourblock__h {
        left: 14%;
        opacity: 1;
    }
}
.whoweare {
    & h1 {       
        font: 700 44px/55px  $bitter;        
        letter-spacing: 0.01em;
        transition: all  1s ease-out;
        text-align: left;
    }
    & button {       
        height: 46px;               
        border: none;
        width: 190px;
        transition: all  1s ease-out;
        color: #ffffff;              
        background: #4285F4;
        box-shadow: 0px 7px 10px rgba(0, 0, 0, 0.22);
        border-radius: 23px;
        font-family: Roboto;
        font-size: 18px;
        font-style: normal;
        font-weight: 700;
        line-height: 21px;
        letter-spacing: 0em;
    } 
    position: relative;    
    &__firstblock {
        background-image: $first_img;
        background-repeat: no-repeat;
        background-size: cover;
        background-color: #ffffff;
        height: 683px;       
        position: relative;
        width: 100%;
        &__header {
            height: 46px;
            width: 72%;
            display: flex;
            justify-content: space-between;
            margin: 30px auto;
            box-sizing: border-box; 
            transform: translateY(20px);  
            & button {
                float: right;               
            }
        }
        &__one,
        &__two {
            width: 361px;
            height: 77px;
            position: absolute;           
            transform: skewX(20deg);
            & p {
                text-align: center;
                font-family: $roboto;
                font-size: 36px;
                font-style: normal;
                font-weight: 300;
                line-height: 77px;
                letter-spacing: 0em;
                color: #ffffff;
                transform: skewX(-20deg);
                & b {
                    font-weight: 500;
                }
            }
        }
        &__one {
            top: 86px;
            left: 700px;          
            background-color: #34A852;          
        }
        &__two {          
            top: 183px;
            left: 733px;
            background-color: #4285F4;          
        }
        & button {
            margin-top: 30px;
        }
        &__title {
            width: 40%;           
            position: absolute;
            top: 70px;
            left: 14%;            
            & h1{               
                & span {
                    color: green;
                }
            }
            & p {
                margin-top: 50px;                
                font-family: $roboto;
                font-size: 22px;               
                font-weight: 300;
                line-height: 34px;
                letter-spacing: 0em;
                text-align: left;

            }
        }
        &__videoBlock {
            margin-left: 10%;
            width: 50%;
            z-index: 10;
            // top: 550px;
            position: relative;
            display: flex;
            justify-content: flex-start;
            align-items: flex-end;
            @media (max-width: $xxl) {
              margin-left: 5%;
              width: 55%;
              align-items: center;
            }
            @media (max-width: $md) {
              opacity: 0;
              visibility: hidden;
            }
            &__wrap {
              width: 514px;
              height: 319px;
              position: relative;
              top: -72px;
              left: 80px;
              transition: 0.5s;
              transform: scale(1);
      
              @media (max-width: $xxl) {
                width: 100%;
                height: 75%;
              }
            }
            &__videoWrap {
              width: 514px;
              height: 319px;
              box-sizing: border-box;
              border: 1px solid #4285F4;             
            }              
            &__videoMenu {
                height: 5%;
                width: 514px;
                background: $blueText;
                border-top-left-radius: 6px;
                border-top-right-radius: 6px;
                display: flex;
                justify-content: flex-end;
                align-items: center;
            }        
            &__full {
                margin-right: 10px;
                display: block;
            }
            & video {
                width: 100%;
                height: 100%;
                -webkit-box-shadow: 0px 10px 23px 0px rgba(0, 0, 0, 0.15);
                -moz-box-shadow: 0px 10px 23px 0px rgba(0, 0, 0, 0.15);
                box-shadow: 0px 10px 23px 0px rgba(0, 0, 0, 0.15);
                border-bottom-left-radius: 6px;
                border-bottom-right-radius: 6px;
                object-fit: cover;
            }              
            &__controllers {
              position: absolute;
              width: 100%;
              height: 100%;
              display: flex;
              justify-content: center;
              align-items: center;
              top: 0;
              &__stopVideoBtn {
                transition: 0.5s;
                opacity: 0;
                visibility: hidden;
              }
              & button {
                background: none;
                border: none;
                width: 72px;
                height: 72px;
                transition: 0.5s;
                &:hover {
                  transform: scale(1.1);
                }
                & img {
                  max-width: 100%;
                  border-radius: 50%;
                }
              }
            }       
        }    
    }
    &__secondblock {
        height: 905px;
        position: relative;
        background-color: #ffffff;        
        background-image: $two_img_back;
        background-repeat: no-repeat;
        background-size: cover;
        &__h {
            position: relative;
            top: -400px;
            transition: all  1s ease-out;
            opacity: 0;
            left: 60%;
            & span {
                color: #4285F4;
            }
        }
        &__button {
            position: absolute;
            left: 14%;
            transition: all  1s ease-out;
            bottom: 0;
            opacity: 0;
        }
        &__p {
            font: 300 26px/48px $roboto;
            position: absolute;
            top: 513px;
            left: -34%;
            opacity: 0;
            transition: all  1s ease-out;
            & b {
                font-weight: 500;
            }
        }
        &__image {
            background-image: $two_img;
            background-repeat: no-repeat;
            background-size: auto;
            background-position: 0 1350%;
            transition: all  1s ease-out;
            height: 600px;
            width: 50%;
            position: absolute;
            right: 0;
            bottom: 0;
        }  
       
    }
    &__threeblock {
        height: 933px;
        z-index: 1;
        position: relative;
        background-image: $three_img_back;
        background-repeat: no-repeat;
        background-size: 100%;
        background-position: -20% 50%; 
        &__h {
            position: absolute;
            top: 822px;
            opacity: 0;
            transition: all  1s ease-out;
            left: 60%;
            & span {
                color: #EA4335F4;
            }
        }
        &__button {
            position: absolute;
            left: 60%;
            bottom: 0px;
            opacity: 0;
            transition: all  1s ease-out;
        }
        &__image {
            background-image: $three_img;
            background-repeat: no-repeat;
            background-size: auto;
            background-position: 100% 450%;
            transition: all  1s ease-out;
            height: 600px;
            width: 50%;
            position: absolute;
            left: 0;
            bottom: 0;
        }  
    }
    &__fourblock {
        height: 933px;
        position: relative;
        background-image: $four_img_back;
        background-repeat: no-repeat;
        background-size: 70%;
        background-position: -20% 50%;
        &__head {
            font: 300 38px/45px $roboto;           
            letter-spacing: 0em;
            text-align: center;
            z-index: 0;
            transition: all  1s ease-out;
            margin: 0 auto;
            transform: translateY(-20px);
            opacity: 0;
            & b {
                font-weight: 400;
            }

        }
        &__h {
            position: absolute;
            top: 25%;
            opacity: 0;
            transition: all  1s ease-out;
            left: -24%;
            & span {
                color: green;
            }
        }
        &__button {
            position: absolute;
            left: 14%;
            bottom: 0;
            opacity: 0;
            transition: all  1s ease-out;
        }
        &__p {
            font: 300 26px/48px $roboto;
            position: absolute;
            top: 413px;
            left: -24%;
            opacity: 0;
            transition: all  1s ease-out;           
        }
        &__image {
            background-image: $four_img;
            background-repeat: no-repeat;
            background-size: auto;
            background-position: 100% 530%;
            transition: all  1s ease-out;
            height: 600px;
            width: 50%;
            position: absolute;
            right:  0;
            bottom: 0;
        }            
    }
}